.card {
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;

  &__image {
    background-color: #ffffff;
    background-image: url(/assets/placeholder.png), url(../../assets/placeholder.png);
    background-size: cover;
  }

  &__delete-btn {
    position: absolute;
    bottom: 5px;
    right: 5px;

    z-index: 1;
    opacity: 30%;

    &:hover {
      opacity: 100%;
    }
  }
}
