$background-color: #78e2e6;

.drop-field {
  position: fixed;
  top: 0;

  height: 100%;
  width: 100%;
  max-width: inherit;

  &--drop-enter {
    background-color: $background-color;
    background-image: url(/assets/download.svg), url(../../assets/download.svg);
    background-repeat: no-repeat;
    background-position: center;
    opacity: 40%;
    z-index: 2;
  }
}
